:root {
  --main-color: #317eeb;
  --hover-color: #2764ba;
  --body-color: #e0e0e0;
  --box-color: #f4f5f8;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App {
  background-color: var(--body-color);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
