:root {
  --main-color: #317eeb;
  --hover-color: #2764ba;
  --body-color: #e0e0e0;
  --box-color: #f4f5f8;
  --dark-grey: #333;
  --light-grey: #aba4a4;
  --dark-theme: #2d3941;
  --hover-grey: #e7e3e3;
}

/* #################################################### */
/* main container */
/* #################################################### */

.main-container,
.sidebar-container,
.chatArea-container,
.sb-conversations,
.sb-header,
.sb-search,
.main-con-container,
.messages-container,
.welcome-container,
.createGroup-container,
.login-container,
.login-box,
.ug-list,
.list-tem,
.text-input-area,
.chatArea-header {
  box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px,
    rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px;
}

.dark {
  background-color: var(--dark-theme) !important;
  color: whitesmoke !important;
}

.main-con-container {
  /* background-color: red; */
  padding: 10px;
  overflow: hidden;
}

.main-con-container:hover .dark {
  background-color: red;
}

.main-con-container:hover:not(.dark) {
  cursor: pointer;
  background-color: #63d7b0 !important;
  scale: 1.02;
}

.main-con-container:active:not(.dark) {
  background-color: #00dc93 !important;
}

.main-con-container.dark:hover {
  transform: scale(1.02);
  cursor: pointer;
}

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.main-container {
  margin: auto;
  background-color: var(--box-color);
  height: 90vh;
  width: 90vw;
  display: flex;
  border-radius: 20px;
  overflow: hidden;
}

/* #################################################### */
/* side bar */
/* #################################################### */

.sidebar-container {
  width: 30%;
  display: flex;
  flex-direction: column;
}

.chatArea-container {
  width: 70%;
}

.chatArea-container .con-title {
  font-size: 1.6rem;
}

.sb-header,
.sb-search,
.sb-conversations {
  background-color: white;
  border-radius: 20px;
  padding: 10px 5px;
  margin: 10px 10px 0px 10px;
  display: flex;
}

.self-timeStamp .date {
  display: none;
}

.self-message-container:hover {
  background-color: #eee;
  border-radius: 20px;
}

.self-message-container:hover .self-timeStamp .date {
  display: inline;
}

.self-message-container .con-lastMessage,
.other-message-container .con-lastMessage {
  font-size: 1.1rem;
}

.other-message-container .con-title {
  font-size: 0.7rem;
}

.other-icons {
  display: flex;
  flex: 1;
  justify-content: space-between;
}

.sb-search {
  align-items: center;
  justify-content: center;
  padding: 5px;
}

.search-box {
  outline-width: 0;
  width: 85%;
  border: none;
  margin-left: 10px;
  font-size: 1.25rem;
  color: #000000a7;
}

.sb-conversations {
  background-color: white;
  border-radius: 20px;
  padding: 10px 20px;
  margin: 10px;
  height: 100%;
  overflow: scroll;
}

.temp {
  width: 100%;
}

.conversation-container {
  display: grid;
  grid-template-columns: 32px auto auto;
  grid-template-rows: auto auto;
  column-gap: 10px;
  margin: 0px 5px 5px 5px;
  border-radius: 20px;
  width: 100%;
}

.con-icon {
  grid-area: 1/1/3/2;
  /* center  */
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--light-grey);

  font-family: "Segoe UI", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;

  font-size: 1.7rem;
  font-weight: bolder;
  color: white;
  height: 36px;
  width: 36px;
  padding: 2px;

  border-radius: 50%;
  justify-self: center;
  align-self: center;
}

.con-title {
  grid-area: 1/2/2/4;
  font-weight: bold;
  font-size: 0.95rem;
  color: rgba(0, 0, 0, 0.507);
}

.con-timeStamp {
  justify-self: flex-end;
  font-size: 0.75rem;
  color: rgba(0, 0, 0, 0.571);
}

.con-lastMessage {
  font-size: 0.75rem;
  color: #0000008d;
}

.main-con-container .con-lastMessage {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* #################################################### */
/* chatarea */
/* #################################################### */

.chatArea-container {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.chatArea-header {
  background-color: white;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px;
  margin: 10px 0px;
  border-radius: 20px;
}

.header-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
}

/* #################################################### */
/* message container */
/* #################################################### */

.messages-container {
  background-color: white;
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-radius: 20px;
  flex: 1;
  margin-bottom: 10px;
  overflow-y: scroll;
  scrollbar-width: thin;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.text-input-area {
  background-color: white;
  padding: 10px;
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
}

.other-message-container {
  border-radius: 20px;
  margin-top: 10px;
}

.other-text-content {
  background-color: var(--body-color);
  width: max-content;
  padding: 10px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  white-space: normal;
}

/* self messags */
.self-message-container {
  display: flex;
  justify-content: flex-end;
}

.messageBox {
  display: flex;
  flex-direction: column;
  background-color: #63d7b0;
  width: max-content;
  padding: 10px;
  margin: 5px;
  border-radius: 20px;
  max-width: 250px;
  word-break: break-word;
}

.self-timeStamp {
  align-self: flex-end;
  padding-top: 2px;
  font-size: 0.7rem;
  font-weight: lighter;
  color: #2d3941;
}

.welcome-logo {
  height: 30vh;
}

.welcome-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  color: var(--dark-grey);
  border-bottom: solid #63d7b0 5px;
  border-radius: 20px;
}

.createGroup-container {
  flex: 1;
  align-self: center;
  background-color: white;
  padding: 20px 10px;
  margin: 10px;
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
}

.login-container {
  height: 90vh;
  width: 90vw;
  background-color: white;
  border-radius: 20px;
  display: flex;
}

.image-container {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--box-color);
}

.image-container img {
  height: 50%;
}

.login-box p:first-child {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.login-box {
  width: 70%;
  border-radius: 20px;
  margin: 10px;
  background-color: white;
  font-weight: bolder;
  color: #63d7b0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.login-box Button {
  margin-top: 20px;
}

.list-container {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.login-box span.ls-link {
  color: #0c336c;
  border-bottom: 2px solid #0c336c;
  cursor: pointer;
}

.login-box p:last-child {
  font-size: 16px !important;
}

.ug-header {
  background-color: white;
  border-radius: 20px;
  padding: 10px 20px;
  margin: 10px;
  align-items: center;
  display: flex;
}

.ug-title {
  color: var(--dark-grey);
  margin: 10px;
  font-weight: bolder;
}

.ug-list {
  flex: 1;
  padding: 10px;
  overflow: scroll;
  margin: 10px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
}

.list-tem {
  height: 64px;
  display: flex;
  gap: 10px;
  align-items: center;
  background-color: white;
  border-radius: 20px;
  padding: 20px;
  margin: 10px;
  transition-duration: 0.1s;
  cursor: pointer;
}

.list-tem:hover {
  background-color: #ddd;
}

.list-tem:active {
  background-color: white;
}

/* .main-container > :last-child {
  width: 70%;
} */

/* media query */

@media only screen and (max-width: 69em) {
  html {
    font-size: 15px;
  }

  .main-container {
    height: 93vh;
    width: 93vw;
  }

  .sb-conversations {
    padding: 10px 10px;
    overflow-x: hidden;
  }
}

@media only screen and (max-width: 61em) {
  html {
    font-size: 13px;
  }
}

@media only screen and (max-width: 54.5em) {
  .main-container {
    height: 95vh;
    width: 95vw;
  }

  .login-box {
    width: 50%;
  }

  .image-container {
    flex: 1;
  }

  .main-container > :first-child {
    width: 48%;
  }
}

@media only screen and (max-width: 40em) {
  .sb-search {
    display: none;
  }
  .sb-conversations {
    display: none;
  }
  .sb-header {
    flex-direction: column;
    flex: 1;
    width: min-content;
  }
  .other-icons {
    flex-direction: column;
  }
  .chatArea-container {
    flex: 1;
  }
  .sidebar-container {
    flex: 0;
  }
  .main-container {
    height: 100vh;
    width: 100vw;
  }

  .login-container {
    /* flex-direction: column-reverse;
    justify-content: center;
    align-items: center; */
    align-items: center;
    justify-content: center;
  }

  .image-container {
    display: none;
  }

  .login-box {
    background-image: url("../social.png");
    height: 60%;
    width: 90%;
  }
}
